
.page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #d4d9dd;
}
.page > main {
    flex-grow: 1;
}
.page > main {
    min-width: 1024px;
    max-width: 1440px;
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    width: 100%;
    align-self: center;
}
#resumeEditor {
  min-width: 27%;
  background: #444;
}
#resumePreview {
  flex-grow: 1;
  margin-left: 33px;
  background: #777;
}



